import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import List from "@mui/material/List";

const dispatchedEvents = [];

const SrsMuiList = function (props, ref) {
  return (
    <List
      dense={props.dense}
      disablePadding={props.disablePadding}
      subheader={props.subheader}
      theme={props.muiTheme}
    >
      <slot />
    </List>
  );
};

const styles = `
  /* Custom styles for the MuiList component */
`;

const props = [
  {
    name: "dense",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disablePadding",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "subheader",
    defaultValue: null,
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-list-item"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiList),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
